@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{font-size:16px;font-family:"Nunito Sans", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-weight:300;background-color:#fafafa;color:#111517}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

header{box-shadow:0 3px #f5f3f3;background-color:#ffffff;height:5vh;min-height:50px}.container{width:80%;min-width:300px;height:100%;margin-inline:auto;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:flex-start;justify-content:flex-start}.titleContainer{display:grid;place-items:center;height:100%}.title{font-size:clamp(0.5rem, 1rem, 2rem);letter-spacing:1px}

section{width:80%;margin-inline:auto;margin-block:1rem}

.searchConatiner{width:100%;min-width:300px;height:50px;margin-inline:auto;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding:0.5rem;background-color:#ffffff;border-radius:5px;box-shadow:0 0 3px #ddd}.searchIron{-webkit-flex:1 1 10%;flex:1 1 10%;display:grid;place-items:center;width:100%;height:100%;cursor:pointer}.formContainer{-webkit-flex:1 1 90%;flex:1 1 90%;height:100%}.formContainer form{width:100%;height:100%}.formContainer form input{width:100%;height:100%;border:none}

.filter{width:60%;max-width:180px;height:50px;margin-top:1rem;background-color:#ffffff;border-radius:5px;font-size:16px;font-weight:600;padding:1rem;box-shadow:0 0 3px #ddd;position:relative}.filterBtn{display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;cursor:pointer}.dropdown{width:100%;position:absolute;top:60px;left:0;background-color:#ffffff;padding:1rem;border-radius:5px;font-size:16px;display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;box-shadow:0 0 3px #ddd}.dropdown ul{list-style:none}.dropdown ul li{padding-block:0.1rem;cursor:pointer}

.card{width:300px;background-color:#ffffff;border-radius:5px;box-shadow:0 0 3px #ddd;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:flex-start;align-items:flex-start;cursor:pointer}.card>.flag{width:100%}.card>.flag>img{max-width:100%;border-top-left-radius:5px;border-top-right-radius:5px}.card>.name{font-size:18px;font-weight:800;padding:1rem}.card>.information{font-size:14px;font-weight:300;padding:1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:flex-start;align-items:flex-start;grid-gap:1rem;gap:1rem}

.cardContainer{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center;grid-gap:2rem;gap:2rem;margin-block:1rem}.link{text-decoration:none;color:#000000}.link:focus,.link:hover,.link:visited,.link:link,.link:active{text-decoration:none}

.detailContainer{min-width:300px;max-width:500px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:1rem;gap:1rem}.detailContainer>.backBtn{width:80px;height:30px;background-color:#ffffff;border-radius:2px;box-shadow:0 0 3px #ddd;cursor:pointer;display:grid;place-items:center}.detailContainer>.flag{width:100%}.detailContainer>.flag>img{max-width:100%}.detailContainer>.name{font-size:18px;font-weight:800;padding:1rem}.detailContainer>.basic{font-size:14px;font-weight:300;padding:1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:flex-start;align-items:flex-start;grid-gap:1rem;gap:1rem}.detailContainer>.extra{font-size:14px;font-weight:300;padding:1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:flex-start;align-items:flex-start;grid-gap:1rem;gap:1rem}.detailContainer>.borders{width:100%;font-size:14px;font-weight:300;padding:1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:flex-start;align-items:flex-start;grid-gap:1rem;gap:1rem}.detailContainer>.borders>.bordersContainer{width:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-flex-wrap:wrap;flex-wrap:wrap;grid-gap:1rem;gap:1rem}.detailContainer>.borders>.bordersContainer>.border{-webkit-flex:0 1 20%;flex:0 1 20%;height:30px;background-color:#ffffff;display:grid;place-items:center;border-radius:2px;box-shadow:0 0 2px #ddd}

